import React, { Component } from 'react'
import { Input,Button} from 'antd';
import managementStyle from "../scss/index.module"
import isLogin from "@/utils/isLogin"
import ManagementTable from "@/components/Table/"
import ManagementButton from "@/components/Button/"

 class Index extends Component {
  state = {
    columns: [
      {
        title: '',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: '',
        dataIndex: 'number',
        key: 'number',
      },
      {
        title: '',
        dataIndex: 'department',
        key: 'department',
      },
      {
        title: '',
        key: 'position',
        dataIndex: 'position'
      },
      {
        title: '',
        key: 'class',
        dataIndex: 'class'
      },
      {
        title: '',
        key: 'kindergarten',
        dataIndex: 'kindergarten'
      },
      {
        title: '()',
        key: 'totalHours',
        dataIndex: 'totalHours'
      },
      {
        title: '()',
        key: 'subsidy',
        dataIndex: 'subsidy'
      },
      {
        title: '()',
        key: 'totalHoursSubsidy',
        dataIndex: 'totalHoursSubsidy'
      }
    ],
    data: [
      {
        key: '1',
        name: 'John Brown',
        number: 32,
        department: 'New York No. 1 Lake Park',
        position: ['nice', 'developer'],
        class: "",
        kindergarten: "",
        totalHours: "",
        subsidy: "",
        totalHoursSubsidy: ""
      }
    ]
  }
    
    render() {
        let {columns,data} = this.state;
        return (
            <div className={managementStyle["ArchivesManagement"]}>
                <header></header>
                <section className={managementStyle["search"]}>
                    <Input placeholder=""/>
                    <Button></Button>
                </section>
                <ManagementButton/>
                <ManagementTable columns={columns} dataSource={data}/>
            </div>
        )
    }
}
export default isLogin(Index)